section.education {
  padding: 20px;
  text-align: center;
  background-color: #f4f4f4;
  margin: 50px auto;
  width: 80%;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.education h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
  color: #333;
}

.education-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; /* Center the cards */
  margin-top: 20px;
}

.education-card {
  background-color: #f4f4f4;
  padding: 20px;
  margin: auto;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  width: 40%; /* Ensures responsive behavior */
  cursor: pointer;
  transition: transform 0.3s ease;
}

.education-card:hover {
  transform: scale(1.05);
}

.education-card h3 {
  font-size: 1.8em;
  color: #555;
  margin-bottom: 10px;
}

.education-card p {
  font-size: 1.1em;
  color: #666;
  line-height: 1.6;
}
.education-card img {
  width: 30%;
  height: auto;
  object-fit: cover;
  border-radius: 0;
  border-radius: 5%;
  margin-bottom: 10px;
}

@media (max-width: 768px) {
  .education-card {
    width: 100%; 
    max-width: none;
  }
}
